import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import About from "./views/About.vue";
import EditEmp from "@/views/EditEmp";
import NewEmp from "@/views/NewEmp";
import ViewEmp from "@/views/ViewEmp";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/about",
      name: "about",
      component: About
    },
    {
      path: "/new",
      name: "new-emp",
      component: NewEmp
    },
    {
      path: "/edit/:employee_id",
      name: "edit-emp",
      component: EditEmp
    },
    {
      path: "/:employee_id",
      name: "view-emp",
      component: ViewEmp
    }
  ]
});
